import api from '../../common/request.js'
import parseHtml from '../../common/html-parser.js'
import { mapState } from 'vuex'
	
export default {
	data() {
		return {
			userId: '',
			bookId: '',
			sectionId: '',
			book: {},
			showSet: false,
			scrollHeight: 0,
			cataloguelist: [],
			showCataMenu: false,
			nightMode: false,
			setFontSize: true,
			fontSize: 30,
			page: 1,
			total: '',
			pickerlist: [],
			pickerIndex: 0,
			preSectionId: '',
			isDisablePre: false,
			nextSectionId: '',
			isDisableNext: false,
			isBookShelf: true
		}
	},
	onLoad() {
		this.bookId = uni.getStorageSync('__bookid__')
		this.sectionId = uni.getStorageSync('__sectionid__') || 0
		this.getReadBookData(this.sectionId)
		this.getBookDirectoryData()
	},
	onReady() {
		const windowHeight = uni.getSystemInfoSync().windowHeight
		let sHeight = windowHeight - 60 - 49
		this.scrollHeight = sHeight * 2
		console.log(this.scrollHeight)
	},
	onShow() {
		if (this.userId === '') {
			this.userId = this.userid ? this.userid : uni.getStorageSync('__userid__')
		}
		this.getIsBookShelf()
	},
	computed: {
		...mapState(['userid'])
	},
	methods: {
		// p标签加样式
		// htmlReplace(txt) {
		//   let result = txt
		//   const regex = new RegExp('<p', 'gi')
		//   result = result.replace(regex, `<p style="padding: 8px 0;"`)
		//   return result
		// },
		setShowBtn() {
			this.showSet = !this.showSet
		},
		nigthModeBtn() {
		  if (this.nightMode) {
		    uni.setNavigationBarColor({
		      frontColor: '#000000',
		      backgroundColor: '#F5EEE9',
		      animation: {}
		    })





		  } else {
		    uni.setNavigationBarColor({
		      frontColor: '#ffffff',
		      backgroundColor: '#292929',
		      animation: {}
		    })





		  }
			this.nightMode = !this.nightMode,
			this.showSet = false
		},
		showCataMenuBtn() {
			this.showCataMenu = !this.showCataMenu,
		    this.showSet = false
		},
		fontSizeBtn() {
			this.setFontSize = !this.setFontSize
		},
		subFontSizeBtn() {
			let num = this.fontSize
			if (num <= 20) {
				return
			}
			num = num - 4
			this.fontSize = num
		},
		addFontSizeBtn() {
		  let num = this.fontSize
		  if (num >= 98) {
		    return
		  }
		  num = num + 4
		  this.fontSize = num
		},
		// 加入书架
		addBookShelf() {
		  if (this.userId) {
		    let para = {
		      user_id: this.userId,
		      book_id: this.bookId
		    }
		    api.post('/user/addUserCase', para).then(res => {
		      if (res.code === 200) {
		        uni.showToast({
		          title: '加入书架成功',
		          duration: 2000
		        })
		        this.isBookShelf = false
		      } else {
		        uni.showToast({
		          title: '加入书架失败',
		          icon: 'none',
		          duration: 2000
		        })
		        this.isBookShelf = true
		      }
		    })
		  } else {
		    // uni.switchTab({
		    //   url: '../user/user'
		    // })
				
				uni.showToast({
				  title: '请先登录',
				  icon: 'none',
				  duration: 2000
				})
		  }
		},
		gotoBookDetails() {
		  uni.navigateTo({
		    url: `../book/book?id=${this.bookId}`
		  })  
		},
		// 选择目录
		selectCatalogue(e) {
		  let id = e.currentTarget.dataset.item.chapter_id
		  this.showCataMenu = false,
		  this.showSet = false
		  this.getReadBookData(id)
		},
		changePickerValue(e) {
		  let index = parseInt(e.detail.value)
		  // console.log(e.detail.value)
		  this.pickerIndex = index,
		  this.page = index + 1
		  this.getBookDirectoryData()
		},
		// 上一章
		preChapter() {
		  // uni.pageScrollTo({
		  //   scrollTop: 0,
		  //   duration: 0
		  // })
		  this.getReadBookData(this.preSectionId)
		},
		// 下一章
		nextChapter() {
		  this.getReadBookData(this.nextSectionId)
		},
		returnBtn() {
		  return
		},
		getIsBookShelf() {
		  let para = {
		    user_id: this.userId,
		    book_id: this.bookId
		  }
		  api.post('/book/bookIsCase', para).then(res => {
		    if (res.code === 200) {
		      let status = res.data.is_case === 'Y' ? false : true
		      this.isBookShelf = status
		    }
		  })
		},
		getReadBookData(sId) {
		  let para = {
		    book_id: this.bookId,
		    user_id: this.userId,
		    chapter_id: sId
		  }
		  api.post('/book/read', para).then(res => {
		    uni.pageScrollTo({
		      scrollTop: 0,
		      duration: 0
		    })
		    if (res.code === 200) {
		      let data = res.data
		      let extra = res.extra
		      // data.content = this.htmlReplace(data.content)
		      data.content = parseHtml(data.content)
		      uni.setStorageSync('__sectionid__', data.chapter_id)
		      uni.setNavigationBarTitle({
		        title: data.chapter_title
		      })
		      this.book = data
		      this.sectionId = data.chapter_id
		      this.preSectionId = extra.pre
		      this.nextSectionId = extra.next
		      if (extra.pre === 0) {
						this.isDisablePre = true
		      } else {
						this.isDisablePre = false
		      }
		      if (extra.next === 0) {
						this.isDisableNext = true
		      } else {
						this.isDisableNext = false
		      }
		    }
		  })
		},
		getBookDirectoryData() {
		  let para = {
		    book_id: this.bookId,
		    page: this.page,
		    limit: 100
		  }
		  api.post('/book/directory', para).then(res => {
		    if (res.code === 200) {
		      // let list = this.pickerlist
		      this.cataloguelist = res.data,
		      this.total = res.extra.count
		      if (this.pickerlist.length === 0) {
		        this.setPickerData(res.extra.count)
		      }
		    }
		  })
		},
		setPickerData(total) {
		  let arr = []
		  let num = Math.floor(total/100)
		  for (let i = 0; i <= num; i++) {
		    let a = (i * 100) + 1
		    let b = (i + 1) * 100
		    if (b > total) b = total
		    let c = a + '-' + b
		    arr.push(c)
		  }
		  this.pickerlist = arr
		}
		
	}
}
